import {NavBar} from "./NavBar";
import {useLocation, useParams} from "react-router-dom";
import {readProjectData} from "./readProjectData";
import {ProjectDetails} from "./ProjectDetails";
import {BackText} from "./BackText";
import {BackToTop} from "./BackToTop";

export const ProjectPage = () => {
    const location = useLocation();
    // const {project_index} = location.state || {}; //  project_index 
    // const {suffix} = location.state || {}; //  project_index 
    const suffix = location.pathname.split('/')[1]; //  suffix projectspublications
    const project_name = location.pathname.split('/')[2]; //  key 
    const projects = readProjectData(suffix);
    const project_index = projects.findIndex(project => project.key === project_name);
    const project = projects[project_index];

    if (!project) {
        return <div>Project Missing</div>;
    }

    return (
        <div className="projectPage">
            <NavBar/>
            <ProjectDetails/>
            <BackText text="BackHomepage"/>
            <BackToTop />
        </div>
    );
}